.track-showcase {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  background: #0a0a0a;
  position: relative;
}

.track-showcase::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 80% 20%, rgba(59, 130, 246, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 20% 80%, rgba(139, 92, 246, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.track-container {
  max-width: 1200px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  z-index: 1;
}

.track-info {
  padding: 2rem;
}

.track-label {
  display: inline-block;
  padding: 0.5rem 1rem;
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.3);
  color: #a78bfa;
  font-size: 0.875rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-bottom: 1.5rem;
  border-radius: 2px;
}

.track-title {
  font-size: clamp(2.5rem, 5vw, 4rem);
  font-weight: 300;
  letter-spacing: 0.05em;
  margin: 0 0 1.5rem 0;
  color: #f5f5f5;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.track-description {
  font-size: 1.125rem;
  line-height: 1.8;
  color: #c0c0c0;
  margin-bottom: 2rem;
  font-weight: 300;
}

.track-meta {
  display: flex;
  gap: 3rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.meta-item {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.meta-label {
  font-size: 0.75rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #808080;
}

.meta-value {
  font-size: 1rem;
  color: #f5f5f5;
}

.track-visual {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.vinyl-disc {
  width: 350px;
  height: 350px;
  border-radius: 50%;
  background: radial-gradient(circle at 30% 30%, #2a2a2a, #0f0f0f);
  position: relative;
  box-shadow:
    0 0 0 2px #1a1a1a,
    0 0 40px rgba(0, 0, 0, 0.5),
    inset 0 0 20px rgba(0, 0, 0, 0.5);
  animation: rotate 20s linear infinite;
}

.vinyl-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 140px;
  height: 140px;
  border-radius: 50%;
  background: radial-gradient(circle, #1a1a1a, #0a0a0a);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow:
    0 0 0 2px #2a2a2a,
    inset 0 0 10px rgba(0, 0, 0, 0.8);
}

.vinyl-label {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(135deg, #8b5cf6, #3b82f6);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 1rem;
  box-shadow: 0 0 20px rgba(139, 92, 246, 0.3);
}

.vinyl-text {
  font-size: 0.7rem;
  font-weight: 600;
  letter-spacing: 0.05em;
  color: #ffffff;
  margin-bottom: 0.25rem;
}

.vinyl-artist {
  font-size: 0.5rem;
  letter-spacing: 0.1em;
  color: rgba(255, 255, 255, 0.8);
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 968px) {
  .track-container {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .track-info {
    text-align: center;
  }

  .track-meta {
    justify-content: center;
  }

  .vinyl-disc {
    width: 280px;
    height: 280px;
  }

  .vinyl-inner {
    width: 110px;
    height: 110px;
  }

  .vinyl-label {
    width: 80px;
    height: 80px;
  }
}
