.streaming-links {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  background: linear-gradient(180deg, #0a0a0a 0%, #1a1a1a 100%);
  position: relative;
}

.streaming-links::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 50% 50%, rgba(139, 92, 246, 0.08) 0%, transparent 50%);
  pointer-events: none;
}

.streaming-container {
  max-width: 1000px;
  width: 100%;
  z-index: 1;
}

.streaming-header {
  text-align: center;
  margin-bottom: 4rem;
}

.streaming-title {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 300;
  letter-spacing: 0.05em;
  margin: 0 0 1rem 0;
  color: #f5f5f5;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.streaming-subtitle {
  font-size: 1.125rem;
  color: #a0a0a0;
  font-weight: 300;
}

.platforms-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  padding: 2rem;
}

.platform-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 2rem 1.5rem;
  background: rgba(26, 26, 26, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  text-decoration: none;
  color: #f5f5f5;
  transition: all 0.3s ease;
  cursor: pointer;
  backdrop-filter: blur(10px);
}

.platform-link:hover {
  background: rgba(139, 92, 246, 0.1);
  border-color: rgba(139, 92, 246, 0.3);
  box-shadow: 0 8px 32px rgba(139, 92, 246, 0.2);
}

.platform-icon {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #8b5cf6;
  transition: transform 0.3s ease;
}

.platform-link:hover .platform-icon {
  transform: scale(1.1);
  color: #a78bfa;
}

.platform-icon svg {
  width: 100%;
  height: 100%;
}

.platform-name {
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.05em;
  text-align: center;
}

@media (max-width: 768px) {
  .platforms-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1.5rem;
    padding: 1rem;
  }

  .platform-link {
    padding: 1.5rem 1rem;
  }

  .platform-icon {
    width: 40px;
    height: 40px;
  }

  .platform-name {
    font-size: 0.875rem;
  }
}
